export default{ 
	data(){
		return{
			banner:{
				indicatorDots: true,
				autoplay: true,
				interval: 4000,
				duration: 800,
				circular:true 
			},
			banner_list:[],
		}
	},
	created(){
		let _this=this;
	},
	mounted(){
		let _this=this;
		//baner
		_this.getBanner();	
	},	
	methods:{
		goMsg(url){
			uni.navigateTo({
				url:url
			})
		},
		
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:14,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner_list=res.data.result.banner;
				}
			},(error) =>{
				
			})
		}
		
	}

}
